import React, { useCallback, useRef, useState, useEffect } from "react";

import * as Tone from 'tone';

function startAudio() {
  Tone.start();
}

function Game() {
  const [leftPaddleY, setLeftPaddleY] = useState(40);
  const [rightPaddleY, setRightPaddleY] = useState(40);
  const [ballX, setBallX] = useState(50);
  const [ballY, setBallY] = useState(50);
  const [ballSpeedX, setBallSpeedX] = useState(1);
  const [ballSpeedY, setBallSpeedY] = useState(1);
  const oscillators = useRef([]);
  const filters = useRef([]);
  const synths = useRef([]);
  const canvasRef = useRef(null);

  const movePaddles = useCallback(() => {
    setLeftPaddleY(prevY => prevY + 1);
    setRightPaddleY(prevY => prevY - 1);
    oscillators.current.forEach(oscillator => {
      oscillator.frequency.value = leftPaddleY * 2;
    });
    filters.current.forEach(filter => {
      filter.frequency.value = rightPaddleY * 10;
    });
  }, [leftPaddleY, oscillators, filters]);

  const moveBall = useCallback(() => {
    setBallX(prevX => prevX + ballSpeedX);
    setBallY(prevY => prevY + ballSpeedY);
    if (ballX >= 100 || ballX <= 0) {
      setBallSpeedX(prevSpeedX => -prevSpeedX);
    }
    if (ballY >= 100 || ballY <= 0) {
      setBallSpeedY(prevSpeedY => -prevSpeedY);
    }
    synths.current.forEach(synth => {
      synth.set({
        'oscillator.frequency': ballX * 10,
      });
      synth.triggerNote && synth.triggerNote(ballX);
    });
  }, [ballSpeedX, ballSpeedY, ballX, ballY, synths]);

  useEffect(() => {
    const interval = setInterval(() => {
      movePaddles();
      moveBall();
    }, 10);
    return () => clearInterval(interval);
  }, [moveBall, movePaddles]);


  return (
    <div className="background-image2">
      <canvas
        width="400"
        height="850"
        ref={canvasRef}
      />
      <button className="label" onClick={startAudio}>Roll Music</button>
    </div>
  );
}

export default Game;
